import { Component, OnInit, Output, EventEmitter , SimpleChanges , OnChanges} from '@angular/core';
import { Options , LabelType } from 'ng5-slider';

@Component({
  selector: 'app-filter',
  templateUrl: './filter.component.html',
  styleUrls: ['./filter.component.css']
})
export class FilterComponent implements OnInit {

  @Output('filterParam') filterEventObj  = new EventEmitter<{}>();
  filterValueParam = {
    minVal : 0,
    maxVal : 0
  }

  constructor() { }

  ngOnInit() {
  }

  ngOnChanges(changes : SimpleChanges){
    console.log(changes);
    
  }

  valueChange(minimumVal,maximumVal){
    //console.log(filterParam,filterParam1);
    this.filterValueParam.minVal = minimumVal;
    this.filterValueParam.maxVal = maximumVal;  
    this.filterEventObj.emit(this.filterValueParam);
  }

  minValue: number = 100;
  maxValue: number = 1000;
  options: Options = {
    floor: 100,
    ceil: 1000,
    translate: (value: number, label: LabelType): string => {
      switch (label) {
        case LabelType.Low:
          this.filterValueParam.minVal = value;
          return '₹' + value;
        case LabelType.High:
          this.filterValueParam.maxVal = value;
          return '₹' + value;
        default:
          return '₹' + value;
      }
    }
  };

}
