import { Component, OnInit, Input , OnChanges , SimpleChanges , EventEmitter , Output} from '@angular/core';
import { ProductListService } from '../../Product/product-list.service';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-shopping-list',
  templateUrl: './shopping-list.component.html',
  styleUrls: ['./shopping-list.component.css']
})
export class ShoppingListComponent implements OnInit {

  @Input('doSortyBy') sortingType : '';
  @Input('serachedKey') searchValue : '';
  @Input('filterObject') flterObjInputParam = {};
  @Output('cartCount') cardCountEvent = new EventEmitter<number>();
  productList : any;
  filterProdList : any;
  addedProduct = [];
  addedProductDetails = [];

  constructor(private http : HttpClient,private _productServiceInstance : ProductListService) {
    let apiResponse = this.http.get("https://api.myjson.com/bins/qzuzi");
    apiResponse.subscribe((response)=>{
      this.productList =  response;
      this.filterProdList = response;
    });
    
   }


  ngOnInit() {
  }

  ngOnChanges(changes : SimpleChanges){     
      
      if((changes.sortingType) && (changes.sortingType.currentValue==="Asc")){
        this.productList = this.productList.sort(function(a, b) {
          return parseFloat(b.price) - parseFloat(a.price);
      }); 
      }else if((changes.sortingType) && (changes.sortingType.currentValue==="Desc")){
        this.productList = this.productList.sort(function(a, b) {
          return parseFloat(a.price) - parseFloat(b.price);
      });
    }

    if(changes.searchValue && typeof this.productList != "undefined"){
      console.log("coming 44");
      
      this.productList = this.searchInArray(changes.searchValue.currentValue,this.productList);
      console.log(this.productList);
      console.log("comig");
    }

    console.log("48");
    console.log(this.productList);
    
    if(changes.flterObjInputParam && typeof this.productList != "undefined"){
      this.productList = this.priceFilterSearch(changes.flterObjInputParam,this.productList);
    }
  }
  
   searchInArray(toSearch, myArray){
    if(toSearch != ''){
      let results = [];
      myArray.forEach((objects)=>{      
      if(objects.name.includes(toSearch)){
          console.log(objects);
          results.push(objects);
        }
      });
      return results;
    }
    return this.productList;
  }

  priceFilterSearch(filterObj,productFilter){
      let minVal = filterObj.currentValue.minVal;
      let maxVal = filterObj.currentValue.maxVal;
      let results = [];
      if(minVal != '' && maxVal != ''){
        
        productFilter.forEach((objects)=>{ 
          console.log(objects.price >= minVal && objects.price <= maxVal);
              
        if(objects.price >= minVal && objects.price <= maxVal ){
          results.push(objects);
        }
        });
        console.log(results.length);
        return results;
      }
      return this.productList;
  }

  addProductToCart(prodElem : any){
    this.cardCountEvent.emit(prodElem.value);
    this.addedProduct.push(prodElem.value);
    //console.log(this.addedProduct);
    var prodCounts = {};
    this.addedProduct.forEach(function(x) { prodCounts[x] = (prodCounts[x] || 0)+1; });
    console.log(prodCounts);

    //let uniqueProductValue =  [...new Set(this.addedProduct)];
    let uniqueProductValue = this.addedProduct.filter(function(item, pos, self) {
      return self.indexOf(item) == pos;
    });
    console.log(uniqueProductValue);

    uniqueProductValue.forEach((value,key)=>{
      let status = false;
      this.filterProdList.forEach((object)=>{
        if(object.id == value){
           this.addedProductDetails.forEach((nestedObject)=>{
            console.log(nestedObject.id,object.id);
            console.log(nestedObject.id==object.id);
            if(nestedObject.id == object.id){
              status = true;
            }
          });
          if(!status){
            this.addedProductDetails.push(object);
          }
        }
      });
    });
    console.log(this.addedProductDetails);
  }

}
